import { Directive, ElementRef   } from '@angular/core';
import { CdsBreadcrumb } from '@cds/core/breadcrumb';

@Directive({ selector: 'cds-breadcrumb' })
export class CdsBreadcrumbDirective {
  protected element: CdsBreadcrumb;



  constructor(elementRef: ElementRef) {
    this.element = elementRef.nativeElement;
  }
}
